      display: block;
      width: 32px;
      height: 32px;
      text-align: center;
      padding: 8px;
      border-radius: 16px;
      transition: 0.15s;
      font-size: 0; }
      .header__nav__btn.no-touch:hover {
        background: #B5E3D8; }
        .header__nav__btn.no-touch:hover .header__nav__btn__line {
          color: #74CBAE; }
      .header__nav__btn.no-touch:active, .header__nav__btn:active {
        opacity: 0.7; }
      .header__nav__btn__lines {
        position: relative;
        width: 16px;
        height: 16px;
        display: inline-block; }
      .header__nav__btn__line {
        position: absolute;
        display: block;
        width: 100%;
        height: 2px;
        background: #fff;
        transition: 0.15s; }
        .header__nav__btn__line--top {
          top: 0;
          left: 0; }
        .header__nav__btn__line--middle {
          top: 50%;
          left: 0;
          margin-top: -1px; }
        .header__nav__btn__line--bottom {
          bottom: 0;
          left: 0; }
      .header__nav__btn.is-active .header__nav__btn__line--top {
        transform: translateY(7px) rotate(45deg); }
      .header__nav__btn.is-active .header__nav__btn__line--middle {
        transform: scaleX(0); }
      .header__nav__btn.is-active .header__nav__btn__line--bottom {
        transform: translateY(-7px) rotate(-45deg); }
      @media (min-width: 48em) {
        .header__nav__btn--menu {
          display: none; } }
      .header__nav__btn--search {
        display: none; }
