/* ==========================================================================
   Variables
   ========================================================================== */

// Color theme

$color-theme: (
  'primary': (
    'base': #74CBAE,
    'light': #B5E3D8,
    'dark': #8EB6AC,
    'darker': #3EA189,
    'shiny': #32C3B0
  ),

  'secondary': (
    'base': #FACD61,
    'light': #FDE093,
    'dark': #E2AF4A
  ),

  'blue': (
    'base': #6A8FB9
  ),

  'red': (
    'base': #ff0000
  ),
  'white': (
    'base': #fff
  ),
  'black': (
    'base': #000,
    'light': #1c1b1b,
    'lighter': #454545,
    'lightest': #7F7F7F
  ),
  'grey': (
    'lighter': #fafafa,
    'light': #eee,
    'base': #ccc,
    'dark': #9b9b9b,
    'darker': #666
  )
);

// Font theme

$font-theme: (
  'text': (
    'extralight': ('Manrope-ExtraLight', sans-serif),
    'light': ('Manrope-Light', sans-serif),
    'base': ('Manrope-Regular', sans-serif),
    'semibold': ('Manrope-SemiBold', sans-serif),
    'medium': ('Manrope-Medium', sans-serif),
    'bold': ('Manrope-Bold', sans-serif),
    'extrabold': ('Manrope-ExtraBold', sans-serif)
  )
);

// Text

$base-font-size: 1rem;
$base-line-height: 1.5rem;

$text-settings: (
  'xxs': (
    font-size: .625rem, // 10px
    line-height: $base-line-height - .5
  ),
  'xs': (
    font-size: .75rem, // 12px
    line-height: $base-line-height - .5
  ),
  's': (
    font-size: .875rem, // 14px
    line-height: $base-line-height
  ),
  'base': (
    font-size: 1rem, // 16px
    line-height: $base-line-height
  ),
  'm': (
    font-size: 1.125rem, // 18px
    line-height: $base-line-height
  ),
  'xm': (
    font-size: 1.5rem, // 24px
    line-height: $base-line-height
  ),
  'l': (
    font-size: 1.75rem, // 28px
    line-height: $base-line-height
  ),
  'xl': (
    font-size: 2rem, // 32px
    line-height: $base-line-height * 2 - .5
  ),
  'xxl': (
    font-size: 2.5rem, // 40px
    line-height: $base-line-height * 2
  ),
  'xxxl': (
    font-size: 3rem, // 48px
    line-height: $base-line-height * 2 + .5
  )
);

// @include text-setting('xs');

// Base line

$bl: 8px;


// Media queries

$mobile: "(min-width: 23.4375em)"; // 375px
$mobile-l: "(min-width: 25.25em)"; // 414px
$tablet: "(min-width: 48em)"; // 768px
$desktop: "(min-width: 64em)"; // 1024px
$desktop-l: "(min-width: 85.375em)"; // 1366px
$desktop-xl: "(min-width: 105em)"; // 1680px

$retina2x: "only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 2/1), only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx)";
$retina3x: "only screen and (-o-min-device-pixel-ratio: 9/4), only screen and (-webkit-min-device-pixel-ratio: 2.25), only screen and (min-device-pixel-ratio: 2.25), only screen and (min-resolution: 2.25dppx)";
