/* ==========================================================================
   CARD
   ========================================================================== */

.card {

  @media #{$tablet} {
    padding: 16px;
  }

  &-container {
    font-size: 0;
    margin: 24px auto;

    @media #{$tablet} { margin: 40px auto; }
    @media #{$desktop} { margin: 40px auto 72px; }

    &__wrapper {
      margin: -8px;

      @media #{$tablet} { margin: -16px; }
    }
  }

  &__figure {
    position: relative;
    display: block;
    width: 100%;
    height: 0;
    margin: 0;
    padding-bottom: 56.66%; // 3:2
    overflow: hidden;
    background: color-theme('white');
    border-width: 1px 1px 0 1px;
    border-style: solid;
    border-color: color-theme('primary', 'shiny');
    

    &__img {
      position: absolute;
      top: 50%;
      left: 50%;

      width: 100%;
      height: auto;

      max-height: none;
      max-width: none;

      min-height: 100%;
      min-width: 100%;

      transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      -webkit-transform: translate(-50%, -50%);

      transition: .2s;

      &.is-wide {
        width: auto;
        height: 100%;
      }
    }

    &.no-touch:hover {
      .card__figure__img { opacity: .8; }
    }

    &.no-touch:active,
    &:active {
      .card__figure__img { opacity: .6; }
    }
  }

  &__video {
    @extend %iframe;
    margin: 0;
    overflow: hidden;
    background: color-theme('white');
    border-width: 1px 1px 0 1px;
    border-style: solid;
    border-color: color-theme('primary', 'shiny');
    

    &__wrapper {
      @extend %iframe__wrapper;
    }
  }

  &__content {
    @extend %child-no-margin;
    padding: 16px;
    background: color-theme('white');
    border-width: 0 1px 1px 1px;
    border-style: solid;
    border-color: color-theme('primary', 'shiny');
    
    @extend %shadow;

    @media #{$tablet} { padding: 24px; }

    > * {
      margin: 16px 0;
    }
  }

  &__date {
    @include text-setting('s');
    color: color-theme('grey', 'dark');
    margin-bottom: -8px;
  }

  &__title {
    font-family: font-theme('text','extralight');
    color: color-theme('grey','darker');
    font-size: 20px;
    text-decoration: none !important;

    &.no-touch:hover {
      color: color-theme('primary', 'shiny');
    }

    &.no-touch:active,
    &:active {
      color: color-theme('primary', 'shiny');
    }
  }

  &__btn {
    margin: 0;
  }
}



#prueba {
  width: 400px;
  height: 400px;
  background-color: #0099CC;
}